<template>
  <section class="container" id="1">
    <div class="row justify-content-center w-100">
      <div id="title">BRANDS</div>
    </div>
    <div class="row w-100">
      <div class="col">
        <div class="card">
          <img src="./../assets/3T.jpeg" class="card-img-top" />
          <div class="card-body">
            <h3>3T Cleaning Products</h3>
            <p class="card-text">
              Products like Car Foam shampoos etc..
            </p>
          </div>
        </div>
      </div>
      <div class="col">
        <div class="card">
          <img src="./../assets/hymax.jpg" class="card-img-top" />
          <div class="card-body">
            <h3>Hymax Cleaning Products</h3>
            <p class="card-text">
              Products like bathroom cleaner etc..
            </p>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'Brands'
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
section {
  height: 70vh;
  scroll-behavior: smooth;
}
#title {
  margin-top: 80px;
  margin-left: -330px;
  color: rgb(12, 8, 8);
  font-size: 3rem;
  font-weight: 800;
  font-family: "Times New Roman";
  text-decoration: underline;
  text-decoration-style: initial;
  text-decoration-color: rgb(235, 141, 27);
  text-decoration-thickness: inherit;
  letter-spacing: 2px;
}
.card {
  height: 350px;
  width: 200px;
  margin-top: 50px;
}
.card-body {
  background-color: #f7f7f7;
}
</style>



